html,
#root,
body,
.App {
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
    background-color: #303045;
    color: $text-color;
}

*,
:after,
:before {
    box-sizing: border-box;
    margin: 0;
    text-decoration: none;
}

ul {
    list-style-type: none;
}

a{
    color: white;
}
