.header {
    background-color: rgba($color: $light-grey, $alpha: 0.15);
    box-shadow: 0 8px 8px -8px $dark-grey;
    display: flex;
    flex-direction: column;
    padding: 15px 10px;
    position: fixed;
    width: 100%;
    z-index: 1;
    &__info-text {
        font-size: 14px;
        color: white;
    }
    &__button {
        height: 100%;
        width: 100%;
        padding: 10px;
        cursor: pointer;
    }
    &__section {
        height: 100%;
        display: none;

        &--active {
            display: block;
        }
    }
    &__upper-list {
        padding: 0;
        & > li {
            margin-bottom: 10px;
        }
    }
}
@media only screen and (min-width: $md) {
    .header {
        align-items: center;
        padding: 30px 20px;

        &__section {
            display: flex;
            align-items: flex-end;
        }
        &__button {
            display: none;
        }
        &__upper-list {
            display: flex;
            margin-right: auto;
            padding: 0;
            & > li {
                margin-right: 20px;
            }
        }
        &__link {
            font-size: 24px;
            font-weight: bold;
            color: white;
            transition: all 0.1s ease-in;
            &:hover {
                color: $light-grey;
                text-decoration: underline;
                font-size: 26px;
            }
        }
    }
}
