.tile {
    &__container {
        width: 320px;
        height: 240px;
        margin: 40px;
        text-align: center;
        margin: 5px;
        background-color: #404055;
        transition: transform 0.2s, background-color 0.33s, border 0.5s;
        overflow: hidden;
        flex-direction: column;

        @media (min-width:$md){
            flex-direction: row;
        }

        @media (min-width: $lg) {
            width: 400px;
            height: 300px;
        }

        &:hover {
            transform: scale(1.2);
            z-index: 1;
            background-color: #505066;
            border: 2px solid white;
        }

        &:hover .tile__upper-box {
            padding-top: 60px;
            height: 60px;
            @media (min-width: $xl) {
                align-items: center;
                height: 100px;
            }
        }

        &:hover .tile__lower-box {
            height: 120px;
            opacity: 1;
        }

        &:hover .tile__image {
            opacity: 0;
            height: 0;
        }
    }
    &__image {
        display: flex;
        background-color: #606075;
        justify-content: center;
        align-items: center;
        height: 50%;
        width: 100%;
        transition: opacity 0.33s, height 0.5s;

        &--size {
            width: 60px;
        }
    }

    &__upper-box {
        display: flex;
        justify-content: center;
        height: 50%;
        width: 100%;
        transition: padding-top 0.5s, height 0.33s;
        box-sizing: content-box;
    }

    &__lower-box {
        height: 0px;
        opacity: 0;
        width: 100%;
        transition: opacity 0.5s, height 0.33s;
    }
    &__headline {
        color: #ccc;
        font-size: 24px;
        font-weight: 800;

        @media (min-width: $md) {
            font-size: 30px;
        }
    }
    &__sub-headline {
        padding: 5px;
        color: #ddd;
        font-size: 16px;

        @media (min-width: $md) {
            font-size: 20px;
        }
    }
}
