.orga {
    &__container {
        padding: 0;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}
@media only screen and (min-width: $md) {
    .orga {
        &__container {
            display: flex;
            padding-top: 140px;
        }
        &__card-container {
            width: 100%;
            display: flex;
            justify-content: center;
        }
    }
}
